// Connect ATX Elite site main JS

document.addEventListener("DOMContentLoaded", () => {
  // --- Scroll to top button ---
  const btn = document.getElementById("backToTop");
  if (btn) {
    btn.addEventListener("click", () =>
      window.scrollTo({ top: 0, behavior: "smooth" })
    );
  }

  // --- Animate headings on scroll (basic fade-in) ---
  document.querySelectorAll("h1, h2").forEach((el) => {
    el.style.opacity = 0;
    el.style.transition = "opacity 0.7s cubic-bezier(.4,0,.2,1)";
  });
  window.addEventListener("scroll", () => {
    document.querySelectorAll("h1, h2").forEach((el) => {
      if (el.getBoundingClientRect().top < window.innerHeight - 60) {
        el.style.opacity = 1;
      }
    });
  });

  // --- Fundraising Meter Bar Animation ---
  const bar = document.getElementById("hero-meter-bar");
  const raisedEl = document.getElementById("funds-raised");
  const goalEl = document.getElementById("funds-goal");

  // Get numbers from DOM (removing $ and commas, just in case)
  const raised = parseFloat((raisedEl?.textContent || "0").replace(/[^0-9.]/g, ""));
  const goal = parseFloat((goalEl?.textContent || "1").replace(/[^0-9.]/g, ""));

  if (bar && goal) {
    // Delay for a nice animation effect
    setTimeout(() => {
      const pct = Math.max(0, Math.min((raised / goal) * 100, 100));
      bar.style.width = pct + "%";
      bar.setAttribute("aria-valuenow", raised);
    }, 350);
  }

  // --- Log for debugging ---
  console.log("🔥 Connect ATX Elite JS loaded and watching changes!");
});

